export default {
	data() {
		return {
			pintuanHomeData: [],
			pages: 0,
		}
	},
	//监听页面加载，其参数为上个页面传递的数据，参数类型为Object（用于页面传参）
	onLoad(options) {
		var that = this;
		that.options = options;
		that.getPintuanhome();
	},
	//监听页面显示。页面每次出现在屏幕上都触发，包括从下级页面点返回露出当前页面
	onShow(){
		
	},
	//监听页面初次渲染完成。注意如果渲染速度快，会在页面进入动画完成前触发
	onReady(){},
	//监听页面隐藏
	onHide(){},
	//监听页面卸载
	onUnload(){},
	//监听用户下拉动作，一般用于下拉刷新
	onPullDownRefresh(){
		this.pages = 0;
		this.pintuanHomeData = [];
		this.getPintuanhome();
	},
	//页面滚动到底部的事件 常用于上拉加载下一页数据
	onReachBottom(){
		this.getPintuanhome();
	},
	methods: { 
		//自定义方法
		getPintuanhome(){
			var that = this;
			this.$http.post("pintuan/pintuan_home",{limit:10,pages:that.pages,goods_id:that.options.goods_id}).then((res)=>{
				console.log("往期拼团记录",res.data);
				that.pintuanHomeData = that.pintuanHomeData.concat(res.data);
				that.pages += 1;
			})
		},
	}		
}
